@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap);
@import url(https://api.mapbox.com/mapbox-gl-js/v2.1.1/mapbox-gl.css);
html,body{margin:0;font-family:'Roboto', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;height:100%;width:100%}ul{padding:0;margin:0}ul li{list-style:none}a{text-decoration:none}a:hover{color:#FFA842}.map-wrapper{position:relative;width:100%;height:100%}.map{position:absolute;top:0;right:0;left:0;bottom:0}.ant-spin-text{color:#FDBF5A}.ant-spin-dot-item{background-color:#FDBF5A}

.container{width:100%;height:100%;background:rgba(28,26,25,0.5);display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.wrapper{background:#FFFFFF;box-shadow:0px 0px 40px rgba(0,0,0,0.1);border-radius:20px;text-align:center;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.login-wrapper{width:580px;min-height:541px}.registration-wrapper{width:580px;min-height:631px}.edit-profile-wrapper{width:888px;min-height:588px}.gotomap-profile-wrapper{width:822px;min-height:345px}.title{font-size:30px}.subtitle{color:#7B7B7B;font-size:18px}.profile-form{width:100%}.form{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;padding:10px 44px 44px;box-sizing:border-box}.form__block{width:100%;margin-bottom:19px;text-align:start}.form__block :last-child{margin-top:0}.form__block--last{margin-bottom:50px}.form__item-title{text-align:start;font-weight:bold}.form__input{width:100%;border:none;border-bottom:2px solid #E4E4E4;height:40px;color:#828282}.form__forgot-password{text-align:end;color:#828282}.form__to-title{color:#828282}.form__to-button{color:#FDBF5A;border:none;background:#fff;cursor:pointer}.form__button{margin-bottom:31px}.form__row{display:-webkit-flex;display:flex;width:100%}.form__col{width:50%;margin-right:35px}.form__col:last-child{margin-right:0}.title-container{margin-top:44px}

.button{width:353px;height:61px;background:#FDBF5A;border-radius:70px;border:none;font-size:25px;cursor:pointer;color:#000;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.button:active{background:#FFA842}.button:hover{color:#000}.button--disabled{background:#D8D7D5;color:#737373;cursor:default}.button--disabled:hover{color:#737373}

.login-page{height:100%;width:100%;display:-webkit-flex;display:flex}.login-page__icon{background:#000;width:33%;height:100%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.login-page__img{width:192px;height:228px}.login-page__map{-webkit-flex:1 1;flex:1 1;background:url(../../static/media/map.ed4fc6db.svg) left top/100% no-repeat;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}

.header{background:#000;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;height:100px;min-height:100px}.header__logo{margin:0 27px}

.menu{display:-webkit-flex;display:flex;margin-right:35px;cursor:pointer}.menu__item{color:#fff;font-size:22px;margin-left:44px}.menu__item--active{color:#FDBF5A}

.route-form{position:absolute;width:486px;min-height:210px;z-index:10;background:#fff;top:160px;left:60px;box-shadow:0px 10px 20px -5px rgba(0,0,0,0.1);border-radius:10px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.route-form__no-data{font-size:20px;text-align:center;margin:24px}.route-form__alert{width:100%;border-radius:10px 10px 0 0}

.select{width:100%;border-bottom:1px solid #ccc}.item-wrapper{padding:10px 20px 0px}.selects-wrapper{width:100%;margin-bottom:24px}

.card-layout{width:347px;height:182px;box-shadow:0px 5px 20px 2px rgba(0,0,0,0.1);border-radius:10px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;padding:20px;box-sizing:border-box;margin-left:50px}.card-layout__header{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.card-layout__content{-webkit-flex:1 1;flex:1 1;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;font-size:21px}.card-layout__footer{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}

.App{height:100%;width:100%}#root{height:100%;width:100%}.page-container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;height:100%}.page-content{-webkit-flex:1 1;flex:1 1;display:-webkit-flex;display:flex}.map-container{height:100%;width:100%}

